* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.doctor-img {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-right: 50px;
}

.doctor-img img {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.08);
  border-radius: 50%;
}

@media (max-width: 700px) {
  .doctor-img {
    width: calc(44% - 50px);
    height: calc(44% - 50px);
  }
}

@media (max-width: 570px) {
  .doctor-img {
    width: calc(36% - 15px);
    height: calc(36% - 15px);
    margin-right: 15px;
  }
}