* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.slider {
  margin-top: 8px;
  margin-bottom: 8px;
}

.slide {
  width: 100%;
  height: 100%;
  margin-right: 8px;
  border-radius: 4px;
  color: #fff;
  text-decoration: none;
}

.slide img {
  width: 95%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
}

div.swiper-button-prev,
div.swiper-button-next {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease-in;
  user-select: none;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 15px !important;
  color: #fff;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  background-color: rgba(0, 0, 0, 0.96);
}

.swiper-button-prev {
  padding-right: 5px;
}

.swiper-button-next {
  padding-left: 5px;
}

@media (max-width: 1024px) {  
  div.swiper-button-prev,
  div.swiper-button-next {
    width: 36px;
    height: 36px;
  }
  
  .swiper-button-prev::after,
  .swiper-button-next::after {
    font-size: 13px !important;
  }

  .swiper-button-prev {
    padding-right: 3px;
  }
  
  .swiper-button-next {
    padding-left: 3px;
  }
}

@media (max-width: 570px) {
  div.swiper-button-prev,
  div.swiper-button-next {
    width: 30px;
    height: 30px;
  }
  
  .swiper-button-prev::after,
  .swiper-button-next::after {
    font-size: 12px !important;
  }

  .swiper-button-prev {
    padding-right: 2px;
  }
  
  .swiper-button-next {
    padding-left: 2px;
  }
}
